.app {
  height: 100%;
  width: 100%;
  font-size: 0.875rem;
  line-height: 1.25rem;
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity, 1));
}

html:is(.dark *),
body:is(.dark *),
#root:is(.dark *),
.app:is(.dark *) {
  --tw-bg-opacity: 1;
  background-color: rgb(0 0 0 / var(--tw-bg-opacity, 1));
}

.dashboard-container {
  padding-left: 2rem;
  padding-right: 2rem;
  padding-top: 2rem;
  padding-bottom: 1.25rem;
}

.mapboxgl-popup-content {
  --tw-bg-opacity: 1 !important;
  background-color: rgb(39 39 42 / var(--tw-bg-opacity, 1)) !important;
  border-radius: var(--radius) !important;
  padding-top: 0.5rem !important;
  padding-bottom: 0.5rem !important;
  padding-left: 0.75rem !important;
  padding-right: 0.75rem !important;
}

.mapboxgl-popup-anchor-top .mapboxgl-popup-tip {
  --tw-border-opacity: 1 !important;
  border-bottom-color: rgb(39 39 42 / var(--tw-border-opacity, 1)) !important;
}

.mapboxgl-popup-anchor-bottom .mapboxgl-popup-tip {
  --tw-border-opacity: 1 !important;
  border-top-color: rgb(39 39 42 / var(--tw-border-opacity, 1)) !important;
}

.mapboxgl-popup-anchor-left .mapboxgl-popup-tip {
  --tw-border-opacity: 1 !important;
  border-right-color: rgb(39 39 42 / var(--tw-border-opacity, 1)) !important;
}

.mapboxgl-popup-anchor-right .mapboxgl-popup-tip {
  --tw-border-opacity: 1 !important;
  border-left-color: rgb(39 39 42 / var(--tw-border-opacity, 1)) !important;
}

.marker-popup {
  --tw-bg-opacity: 1;
  background-color: rgb(39 39 42 / var(--tw-bg-opacity, 1));
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity, 1));
  padding: 0px;
  margin: 0px;
  display: flex !important;
  justify-content: space-between;
  align-items: center;
  gap: 0.75rem;
}

.marker-popup-image {
  height: 2.5rem;
  width: 2.5rem;
  border-radius: var(--radius);
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity, 1));
  object-fit: cover;
}

.marker-popup-price {
  font-size: 0.875rem;
  line-height: 1.25rem;
  font-weight: 600;
  --tw-text-opacity: 1;
  color: rgb(224 224 226 / var(--tw-text-opacity, 1));
}

.marker-popup-title {
  cursor: pointer;
}

.marker-popup-title:hover {
  --tw-text-opacity: 1;
  color: rgb(147 197 253 / var(--tw-text-opacity, 1));
  text-decoration-line: underline;
}

.marker-popup-title:focus {
  outline: 2px solid transparent;
  outline-offset: 2px;
}

.marker-popup-price-unit {
  font-size: 0.75rem;
  line-height: 1rem;
  font-weight: 400;
  --tw-text-opacity: 1;
  color: rgb(130 130 139 / var(--tw-text-opacity, 1));
}

/* Amplify UI Overrides */
[data-amplify-authenticator] {
  --amplify-components-button-primary-background-color: var(--primary);
  --amplify-components-button-primary-hover-background-color: hsl(
    var(--primary) / 0.9
  );
  --amplify-components-button-border-radius: var(--radius);
  --amplify-components-fieldcontrol-border-radius: var(--radius);
}

[data-amplify-authenticator][data-variation='default'] {
  height: 100%;
  padding: 2rem !important;
}

[data-amplify-authenticator] [data-amplify-router] {
  border: none !important;
  box-shadow: none !important;
  max-width: 400px !important;
  margin: 0 auto;
}

[data-amplify-authenticator] [data-amplify-container] {
  border-radius: var(--radius);
  padding: 2rem !important;
  border: 1px solid hsl(var(--border));
}

[data-amplify-authenticator] [data-amplify-form] {
  padding: 0 !important;
}

[data-amplify-authenticator] .amplify-tabs__list {
  display: none;
}

[data-amplify-authenticator] .amplify-button--primary {
  width: 100%;
  height: 2.75rem;
  margin-top: 1rem;
  background-color: hsl(var(--primary)) !important;
  color: hsl(var(--primary-foreground)) !important;
  font-weight: 500;
  font-size: 0.875rem;
  line-height: 1.25rem;
}

[data-amplify-authenticator] .amplify-button--primary:hover {
  background-color: hsl(var(--primary) / 0.8) !important;
}

[data-amplify-authenticator] .amplify-field-group__control {
  border-color: hsl(var(--input));
}

[data-amplify-authenticator] .amplify-field-group__control:focus-within {
  border-color: hsl(var(--ring)) !important;
  box-shadow: 0 0 0 1px hsl(var(--ring)) !important;
}

[data-amplify-authenticator] .amplify-field__show-password {
  color: hsl(var(--muted-foreground));
}

[data-amplify-authenticator] .amplify-label {
  font-size: 0.875rem;
  line-height: 1.25rem;
  font-weight: 500;
  color: hsl(var(--foreground));
}

[data-amplify-authenticator] .amplify-select {
  border-color: hsl(var(--input));
  border-radius: var(--radius);
  height: 2.5rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
}

[data-amplify-authenticator] .amplify-text--error {
  color: #ff0000;
}

